<template>
	<div id="auth-login">
		<div class="content">
			<div class="plateform">
				<div class="plateform-bg bg-primary"></div>
				<div class="plateform-info">
					<div class="logo-wrap">
						<img class="img-100" :src="config.logo" alt="">
					</div>
					<div class="title">{{config.title}}</div>
				</div>
			</div>
			<div class="inputs-group">
				<input v-model="username" type="text" placeholder="请输入手机号">
				<input v-model="password" type="password" placeholder="请输入登录密码">
			</div>
			<van-button class="btn-login" size="large" type="primary" :disabled="!islegal" block @click="onSubmit">登录</van-button>
		</div>
	</div>
</template>

<script>
	import {getData, setData} from "@/controller/funcCommon.js"
	export default {
		data() {
			return {
				config: {
					logo: '',
					title: ''
				},
				username: '',
				password: '',
				islegal: false,
			}
		},
		methods: {
			onLoad() {
				var that = this;
				getData({
					vue: that,
					url: 'plateform/auth/login',
					autoAssign: true,
					variable: 'config',
					success: function(res) {
						that.islegal = true;
					}
				})
			},
			onSubmit() {
				var that = this;
				if(!that.islegal) {
					return;
				}
				if(!that.username) {
					that.util.$toast('请输入手机号');
					return;
				}
				if(!that.password) {
					that.util.$toast('请输入登录密码');
					return;
				}
				that.islegal = false;
				setData({
					vue: that,
					url: 'plateform/auth/login',
					data: {
						username: that.username,
						password: that.password
					},
					success: function(result) {
						that.util.setStorage('ipuser', result.user);
						that.util.setStorage('iptoken', result.user.token);
						var redirct_url = window.redirct_url;
						if(!redirct_url || (redirct_url && redirct_url.path.indexOf('pages/auth/') != -1)) {
							redirct_url = {path: '/pages/order/takeout'};
						}
						that.$router.replace(that.util.getUrl(redirct_url));
						return;
					},
					fail: function(result) {
						that.util.$toast(result.message);
						that.islegal = true;
					},
				})
			}
		},
		mounted() {
			this.onLoad();
		}
	}
</script>

<style>
	#auth-login .content{
		background-color: #fff;
	}
	#auth-login .plateform{
		position: relative;
		width: 100%;
		overflow: hidden;
		height: 200px;
		margin-bottom: 50px;
	}
	#auth-login .plateform .plateform-bg{
		width: 1200px;
		height: 1200px;
		border-radius: 50%;
		position: absolute;
		z-index: 1;
		bottom: 0;
		left: 50%;
		transform: translateX(-50%);
	}
	#auth-login .plateform-info{
		position: absolute;
		z-index: 10;
		top: 0;
		left: 0;
		right: 0;
	}
	#auth-login .plateform .logo-wrap{
		display: block;
		margin: 50px auto 20px;
		width: 80px;
		height: 80px;
		border-radius: 50%;
		overflow: hidden;
		font-size: 0px;
		border: 2px solid #fff;
		padding: 2px;
	}
	#auth-login .plateform-info .title{
		color: #fff;
		text-align: center;
		font-weight: 500;
	}
	#auth-login .inputs-group input{
		display: block;
		width: 70%;
		margin: 0 auto;
		border-bottom: 1px solid #eee;
		font-size: 15px;
		padding: 20px 10px 10px;
		margin-bottom: 10px;
	}
	#auth-login .btn-login{
		width: 70%;
		border-radius: 100px;
		margin: 50px auto 0;
	}
</style>


